.footer {
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 60px;
    background: #1e1e1e;
    display: flex;
    place-items: center;
    justify-content: center;
    gap: 40px;
    color: white;
}
.footer a {
    scale: 1.3;
    color: white;
    text-decoration: none;
    font-family: sans-serif;
}