body {
  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  background: linear-gradient(45deg,#8b8b8b,#ababab);
  padding: 2em;
  font: bold 17px sans-serif;
}
.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
}

.card {
  display: flex;
  box-sizing: border-box;
  width: 260px;
  background: #1e1e1e;
  color: white;
  line-height: 60px;
  border-radius: 50px 15px 15px 50px;
  padding: 1em;
  padding-left: 1.5em;
  box-shadow: 0px 0px 20px #1e1e1e80;
  animation: ofset .4s alternate ease-in;
}
@keyframes ofset {
  0% {
    opacity: 0;
  }
}
.imgspace {
  margin-left: -10px;
  margin-right: 10px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: white;
  display: grid;
  place-items: center;
}
.imgspace img {
  width: 43px;
  height: 43px;
}
a {
  text-decoration: none;
}







.addingCard {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  display: grid;
  place-items: center;
  backdrop-filter: blur(10px);
  animation: blur .4s alternate;
}
@keyframes blur {
  0% {
    backdrop-filter: blur(0px);
    opacity: 0;
  }
}

.plus {
  scale: 2;
  color: #1e1e1e;
}
.ed {
  scale: 1.2;
}

.details {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}
.details input, .details button {
  box-sizing: border-box;
  padding: 0px 20px;
  border: 3px transparent dashed;
  outline: none;
  width: 280px;
  line-height: 50px;
  border-radius: 15px;
  color: white;
  background: #1e1e1e;
  font-size: 17px;
  transition: all 0.1s;
}
.details button {
  cursor: pointer;
}
.details .color-picker {
  height: auto;
}
::placeholder {
  color: #b9b9b9;
}